.card
{
  border: 1px solid #f3f3f3;
  padding: 20px;
  width: 220px;
  border-radius: 40px;
  margin-right: 30px;
  transition: box-shadow 0.2s ease-in-out, transform .2s ease-in-out;

  &:hover{
    transform: translateY(-5px);
    box-shadow: 0 20px 30px rgba(0,0,0,0.06);
  }
  .selected
  {
    cursor: pointer;
  }
  span
  {
    font-size: 13px;
    opacity: 0.5;
    text-transform: uppercase;
  }
  b
  {
    font-size: 13px;
  }
  h5
  {
    font-weight: 400;
    font-size: 14px;
  }
}